



































































































import { Component, Vue } from 'vue-property-decorator'
import { getCompanyList, addCompany, putCompany, delCompany } from '@/api/admin'
import { IArticleData } from '@/api/types'
import { Form } from 'element-ui'

@Component({
  name: 'Company'
})
export default class extends Vue {
  private list: IArticleData[] = []
  private listLoading = true
  private dialogFormVisible = false
  private title = '新增企业'
  private company = ''
  private total = 0
  private sub = ''
  private listQuery = {
    company: '',
    page: 1,
    pagesize: 10
  }
  private initForm = {
    company: '',
    abbreviation: '',
    companyaddress: '',
    companyphone: '',
    managementname: '',
    managementphone: '',
    companytype: '',
    leader: '',
    leaderphone: '',
    message: '',
    webaddress: ''
  }
  private form = { ...this.initForm }
  private rules = {
    company: [
      { required: true, message: '请输入企业名称', trigger: 'blur' }
    ],
    abbreviation: [
      { required: true, message: '请输入企业简称', trigger: 'blur' }
    ]
  }
  private formLabelWidth = '120px'

  created() {
    this.getList()
  }
  private async getList() {
    this.listLoading = true
    const { data } = await getCompanyList(this.listQuery)
    this.list = data.items
    this.total = data.total
    this.listLoading = false
  }
  // 新增 修改
  private async changeHandel(row:any) {
    this.dialogFormVisible = true
    if (row) {
      this.title = '修改企业'
      this.sub = 'put'
      this.form = row
    } else {
      this.title = '新增企业'
      this.sub = 'add'
      this.form = this.initForm
    }
  }
  // 新增修改 保存
  private async submitHandle() {
    (this.$refs.form as Form).validate(async(valid) => {
      if (valid) {
        if (this.sub == 'add') {
          const res = await addCompany(this.form)
          if (res.status === 200) {
            this.$message({
              type: 'success',
              message: '添加成功!'
            })
            this.getList()
          }
        } else if (this.sub === 'put'){
          const res = await putCompany(this.form)
          if (res && res.status === 200) {
            this.$message({
              type: 'success',
              message: '修改成功!'
            })
            this.getList()
          }
        }
        this.dialogFormVisible = false
      }
    })
  }
  // 删除
  private async delHandel(id:any) {
    const res  = await delCompany({ id })
    console.log(res)
    // return
    if(res.hasOwnProperty("status")){
      if(res.status == 200) {
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.getList()
      }
    }


    // this.$confirm('确定要删除该企业？', '提示', {
    //   confirmButtonText: '确定',
    //   cancelButtonText: '取消',
    //   type: 'warning'
    // }).then(() => {
    //   const res  =  delCompany({ id })
    //
    //   console.log(res)
    //   // return
    //   if(res.hasOwnProperty("status")){
    //     if(res.status == 200) {
    //       this.$message({
    //         type: 'success',
    //         message: '删除成功!'
    //       })
    //       this.getList()
    //     }
    //   }
    // }).catch(err => {
    //   console.log(err)
    //   this.$message({
    //     type: 'info',
    //     message: '已取消删除'
    //   })
    // })
  }
  // 分页切换
  private async handleCurrentChange(page:any) {
    this.listQuery. page = page
    this.getList()
  }
}
